import React, { Component } from 'react';

class Legend extends Component {
    render() {
        return (
            <div id="legend-box">
                <p><span className="lv0 legend"></span></p>
                <p><span className="lv1 legend"></span></p>
                <p><span className="lv2 legend"></span></p>
                <p><span className="lv3 legend"></span></p>
                <p><span className="lv4 legend"></span></p>
                <p><span className="lv5 legend"></span></p>
            </div>
        );
    }
}
export default Legend;


